import React, { Component } from 'react';

const container = { display: 'flex', height: '100vh', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' };
const button = {
	backgroundColor: '#414288',
	color: '#ffffff',
	borderRadius: 100,
	padding: 10,
	paddingLeft: 20,
	paddingRight: 20,
	fontSize: 14,
	fontWeight: 'bold',
	cursor: 'pointer',
};
var header = {color: '#414288',fontSize: 20};
const row = {display: 'flex', flexDirection: 'row', padding: 10};
const title = {fontSize:16, color:'#414288'};
const input1= {fontSize:16, color:'#414288', paddingLeft:20};
const  input2= {fontSize:16, color:'#414288', paddingLeft:38};
const input3= { paddingLeft:148};

export class Login extends Component {
	login() {
		const url = 'http://localhost:8010/Login';
		let data = {
			"username" : "",
			"password" :""
		}
		let fetchData = { 
			method: 'POST', 
			body: data,
			headers: new Headers()
		}
		
		fetch(url, fetchData)			
		.then((resp) => resp.json()
		.then((data) => {
			console.log(data.body.custId);
			console.log(data.body.password);
			console.log(data);
			console.log(this.props);
			if(data.status==="SUCCESS")
				this.props.history.push('/home');
			else
				this.props.history.push('/errorr');
		})
		.catch(function(error) {
			console.log(error);
		}));
	}
render() {
	return (
		<div style={container}>
			<div style={header}></div>
			<form method="post" action="http://localhost:8010/Login">
				<div style={row}>
					<div style={title}>
						Customer ID
			 		 </div>
					<div style={input1}>
						<input type="text" name="username"/>
					</div>
				</div>
				<div style={row}>
					<div style={title}>
						Password
			  		</div>
					<div style={input2}>
						<input type="password"  name="password" />
					</div>
				</div>
				<div style={input3}>
					<input style={button} type="submit" value="LOGIN" onClick={() => this.login()}/>
				</div>	
			</form>
		</div>
	);
	
	}
}



